import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

const initialState = {
  servicesData: localStorage.getItem("servicesData")
    ? JSON.parse(localStorage.getItem("servicesData"))
    : {},
};

export const getServiceQnA = createAsyncThunk(
  "getServiceQnA",
  async (req, body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      const { data } = await axios.get(
        `${API_URL}/api/services/getQnA/${req.serviceId}`,
        body,
        config
      );
      return await data;
    } catch (error) {
      console.log(error);
    }
  }
);

export const createServiceQnAData = createAsyncThunk(
  "createuserservices",
  async (body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      if (body.id) {
        const { data } = await axios.post(
          `${API_URL}/api/services/updateQnA`,
          body,
          config
        );
        return await data;
      } else {
        const { data } = await axios.post(
          `${API_URL}/api/services/createQnA`,
          body,
          config
        );
        return await data;
      }
    } catch (error) {
      console.log(error);
    }
  }
);

