export const getUserServicesData = createAsyncThunk(
  "getuserservices",
  async (req, body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      const { data } = await axios.get(
        `${API_URL}/api/services/details/${req.token}`,
        body,
        config
      );
      return await data;
    } catch (error) {
      console.log(error);
    }
  }
);
export const createUserServicesData = createAsyncThunk(
  "createuserservices",
  async (body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      if (body.id) {
        const { data } = await axios.post(
          `${API_URL}/api/services/update`,
          body,
          config
        );
        return await data;
      } else {
        const { data } = await axios.post(
          `${API_URL}/api/services/create`,
          body,
          config
        );
        return await data;
      }
    } catch (error) {
      console.log(error);
    }
  }
);

export const deleteUserServiceData = createAsyncThunk(
  "deleteuserservice",
  async (body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      const { data } = await axios.post(
        `${API_URL}/api/services/delete`,
        body,
        config
      );
      return await data;
    } catch (error) {
      console.log(error);
    }
  }
);

export const serviceReducer = createSlice({
  name: "userServices",
  initialState,
  reducers: {},
  extraReducers: {
    [getUserServicesData.fulfilled]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
        state.homeScreenSuccess = "";
      } else {
        state.userService = action.payload;
        state.success = "";
      }
    },
    [getUserServicesData.pending]: (state, action) => {
      state.homeScreenLoading = true;
      state.userService = null;
      state.homeScreenSuccess = "";
      state.homeScreenError = "";
    },
    [getUserServicesData.rejected]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.userService = null;
        state.homeScreenError = action.payload.error;
      }
    },
    [createUserServicesData.fulfilled]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
        state.homeScreenSuccess = "";
      } else {
        state.userService = action.payload;
        state.success = "";
      }
    },
    [createUserServicesData.pending]: (state, action) => {
      state.homeScreenLoading = true;
      state.homeScreenSuccess = "";
      state.homeScreenError = "";
    },
    [createUserServicesData.rejected]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
      }
    },
    [deleteUserServiceData.fulfilled]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
        state.homeScreenSuccess = "";
      } else {
        state.userService = action.payload;
        state.success = "";
      }
    },
    [deleteUserServiceData.pending]: (state, action) => {
      state.homeScreenLoading = true;
      state.homeScreenSuccess = "";
      state.homeScreenError = "";
    },
    [deleteUserServiceData.rejected]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
      }
    },
  },
});

export default serviceReducer.reducer;
