import React, { Fragment, useEffect, useRef, useState } from "react";
import {
  Container,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
} from "reactstrap";
import { getUserServicesData } from "../../reducers/serviceReducer";
import { useDispatch, useSelector } from "react-redux";

import ServiceViewDesc from "../../shared-components/serviceView/serviceViewDesc";
import FileComponent from "../../shared-components/FileComponent";

const Service = (props) => {
  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [serviceData, setServiceData] = useState(null);
  const [activeTab, setActiveTab] = useState(null);
  const elementRef = useRef(null);

  const scrollToElement = () => {
    if (elementRef.current) {
      const elementPosition = elementRef.current.offsetTop - 50;
      window.scrollTo({
        top: elementPosition,
        behavior: "smooth",
      });
    }
  };

  const toggleTab = (tab) => {
    if (activeTab !== tab) {
      setActiveTab(tab);
      scrollToElement();
    }
  };

  const getUserService = async () => {
    const data = await dispatch(
      getUserServicesData({ token: userdetails.token })
    );
    if (!data.payload.error) {
      await setServiceData(data.payload);
      //await setActiveTab(data.payload[0]._id);
    } else {
      await setServiceData(null);
    }
  };

  useEffect(() => {
    setActiveTab(props.activeId ? props.activeId : null);
    getUserService();
  }, [userdetails, props.activeId]);

  return (
    <>
      <Container className="blogTech">
        <h3 className="t-color f-size-36 py-3 my-4 text-center">
          IT Services: Empowering Solutions for a Digital World
        </h3>
        <div className="blogTech-container">
          {serviceData && (
            <div className="d-flex">
              <div className="pt-4">
                <Nav pills vertical>
                  {serviceData.map((item, i) => (
                    <Fragment key={`pill${item._id}`}>
                      <NavItem>
                        <NavLink
                          className={`text-nowrap p-1 ${
                            activeTab === item._id ? "active" : ""
                          }`}
                          onClick={() => toggleTab(item._id)}
                        >
                          <span className="d-none d-md-block">
                            {item.serviceName}
                          </span>
                          <span className="avatar d-block d-md-none">
                            {item.serviceImage ? (
                              <FileComponent
                                fileSrc={item.serviceImage}
                                fileClass=""
                                alt={item.serviceName}
                              />
                            ) : (
                              ""
                            )}
                          </span>
                        </NavLink>
                      </NavItem>
                    </Fragment>
                  ))}
                </Nav>
              </div>
              <div
                className="flex-grow-1 p-2 p-md-4 pt-4 t-back2"
                ref={elementRef}
              >
                <TabContent activeTab={activeTab}>
                  {serviceData.map((item, i) => (
                    <TabPane tabId={item._id} key={`tab${item._id}`}>
                      <ServiceViewDesc item={item} />
                    </TabPane>
                  ))}
                </TabContent>
              </div>
            </div>
          )}
        </div>
      </Container>
    </>
  );
};

export default Service;
