import React from "react";
import Base from "../Base";
import Service from "../components/ServicesView/Services";
import { useParams } from "react-router-dom";

const Services = () => {
  const { id } = useParams();
  return (
    <>
      <Base>
        <div className="container-fluid px-0">
          <Service activeId={id} />
        </div>
      </Base>
    </>
  );
};

export default Services;
