import React, { Fragment, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { getServiceQnA } from "../../reducers/serviceReducer";
import AnimatedElement from "../../shared-components/AnimatedElement";
import ServiceQnASection from "./ServiceQnASection";
import { setLoading } from "../../reducers/commonReducer";

const ServicesQnA = (props) => {
  const serviceId = props.serviceId;
  const [serviceQnAList, setServiceQnAList] = useState(null);
  const dispatch = useDispatch();

  const getServiceQnAList = async (serviceId) => {
    dispatch(setLoading(true));
    const data = await dispatch(getServiceQnA({ serviceId }));
    if (!data.payload.error) {
      await setServiceQnAList(data.payload);
    } else {
      await setServiceQnAList(null);
    }
    dispatch(setLoading(false));
  };

  useEffect(() => {
    getServiceQnAList(serviceId);
  }, []);

  return (
    <div className="container-fluid p-0 profile">
      <div className="px-md-3">
        {serviceQnAList &&
          serviceQnAList.map((item, i) => (
            <Fragment key={item._id}>
              <AnimatedElement>
                <ServiceQnASection item={item} index={i + 1} />
              </AnimatedElement>
            </Fragment>
          ))}
      </div>
    </div>
  );
};

export default ServicesQnA;
